<template>
  <div class="krry-main">
    <!--  -->
    <template v-if="warehousesList.length">
      <el-checkbox-group v-model="warehouseObj" @change="handleWhChange">
        <el-checkbox :label="item" v-for="(item,index) in warehousesList" :key="index">{{item.name}}</el-checkbox>
      </el-checkbox-group>
    </template>
    <!--  -->
    <krry-container
      :wareHousePro="provinceList"
      :wareHouseFlag="wareHouseFlag"
      :boxTitle="boxTitle"
      :boxOperation="boxOperation"
      :dataObj="dataObj"
      :selectedData="selectedData"
      :onChangeSelected="emitChangeSelected"
    ></krry-container>
  </div>
</template>

<script>
import krryContainer from './models/container'
export default {
  name: 'kr-cascader',
  props: {
    warehousesList: {
      type: Array,
      default: () => []
    },
    boxTitle: {
      type: Array,
      default: () => ['', '', '', '']
    },
    boxOperation: {
      type: Array,
      default: () => [
        '',
        '',
        '',
        ''
      ]
    },
    dataObj: {
      type: Object,
      default: () => {}
    },
    selectedData: {
      type: Array,
      default: () => []
    }
  },
  components: {
    krryContainer
  },
  data() {
    return {
      hasSelectData: [],
      warehouseObj: [], // 
      warehouse: [], // 
      provinceList: [], // id
      wareHouseFlag: false // id
      // warehousesList: [
      //   // 
      //   {
      //     key: "NH",
      //     name: "",
      //     province: [
      //       "101112" // id
      //     ]
      //   },
      //   {
      //     key: "SH",
      //     name: "",
      //     province: ["101104", "101105"]
      //   },
      //   {
      //     key: "CD",
      //     name: "",
      //     province: ["101110"]
      //   },
      //   {
      //     key: "BJ",
      //     name: "",
      //     province: ["101106", "101107"]
      //   },
      //   {
      //     key: "HZ",
      //     name: "",
      //     province: ["101108", "101109"]
      //   }
      // ]
    }
  },
  created() {
    this.getWareHouses()
  },
  methods: {
    // 
    async getWareHouses() {
      // 
      this.warehouseObj = this.warehousesList // 
      this.warehouse = this.warehousesList.map(val => val.key) // id

      this.wareHouseFlag = true // 
      // this.warehousesList [{key:'', province:[],},...]
      for (let val of this.warehousesList) {
        // 
        this.provinceList = this.provinceList.concat(
          val.province.map(vq => vq.trim())
        )
      }
    },
    // value - 
    handleWhChange(value) {
      this.warehouse = value.map(val => val.key)
      // 
      this.provinceList = []
      // value [{key:'', province:[],},...]
      for (let val of value) {
        // 
        this.provinceList = this.provinceList.concat(
          val.province.map(vq => vq.trim())
        )
      }
    },
    // 
    emitChangeSelected(val) {
      this.hasSelectData = val
      this.$emit('onChange', val)
    },
    // 
    getSelectedData() {
      return this.hasSelectData
    }
  }
}
</script>

<style lang="scss" scoped>
.krry-main {
  min-width: 906px;
}
</style>



